import React from "react";
import { useQuery } from "react-query";
import { useOutletContext } from "react-router-dom";
import { fetchCoinHistory } from "../api";
import styled from "styled-components";
import { stringify } from "querystring";
import PriceItem from "../PriceItem";

const Container = styled.div`
  max-width: 480px;
  margin: 0 auto;
`;

interface IHistorical {
    time_open: string;
    time_close: string | number;
    open: string | number;
    high: string | number;
    low: string | number;
    close: number;
    volume: string | number;
    market_cap: number;
}

interface IError {
    error? : string;
}

function Price() {
    const coinId = useOutletContext<string>();
    const { isLoading, data } = useQuery<IHistorical[]>(
        ["ohlcv", coinId],
        () => fetchCoinHistory(coinId),
        {
            refetchInterval: 20000,
        }
    );

    const errorData = data as IError;
    return (
        <Container>
            {(data && Array.isArray(data)) ?
                data?.map((price) => (
                    <PriceItem
                        key={price.time_close}
                        time={(typeof price.time_close === 'string') ? price.time_close : price.time_close.toString()}
                        open={(typeof price.open === 'string') ? Number(price.open) : price.open}
                        high={(typeof price.high === 'string') ? Number(price.high) : price.high}
                        low={(typeof price.low === 'string') ? Number(price.low) : price.low}
                        close={(typeof price.close === 'string') ? Number(price.close) : price.close}
                    />
                ))
            : <>There is An Error Occured While Get Price Data: {errorData?.error ?? 'Price Data can not be loaded.'}</>
            }
        </Container>
    );
}

export default Price;
