import { useQuery } from "react-query";
import { useOutletContext, useParams } from "react-router-dom";
import { fetchCoinHistory } from "../api";
import ApexCharts from "react-apexcharts";
import { ApexOptions } from "apexcharts";
import styled from "styled-components";
import { useState } from "react";
import { useRecoilValue } from "recoil";
import { isDarkAtom } from "../atom";

interface IHistorical {
    time_open: string;
    time_close: string | number;
    open: string | number;
    high: string | number;
    low: string | number;
    close: number;
    volume: string | number;
    market_cap: number;
}

const Container = styled.div`
  padding: 0px 20px;
  max-width: 480px;
  margin: 0 auto;
`;

const ToggleBtn = styled.button``;

function Chart() {
    const coinId = useOutletContext<string>();
    const { isLoading, data, error } = useQuery<IHistorical[]>(
        ["ohlcv", coinId],
        () => fetchCoinHistory(coinId),
        {
            refetchInterval: 20000,
        }
    );

    console.log(data);
    const [isCandle, setCandle] = useState(false);
    const isDark = useRecoilValue(isDarkAtom);

    const lineChartOptions: ApexOptions = {
        theme: {
            mode: "dark",
        },
        chart: {
            type: "line",
            height: 500,
            width: 500,
            toolbar: { show: false },
            background: "transparent",
        },
        grid: { show: false },
        stroke: {
            curve: "smooth",
            width: 5,
        },
        yaxis: {
            show: false,
        },
        xaxis: {
            labels: {
                show: false,
            },
            axisBorder: {
                show: false,
            },
            axisTicks: {
                show: false,
            },
            type: "datetime",
            categories: data?.map((price) => typeof(price.time_close) === 'string' ? Number(price.time_close) * 1000 : price.time_close * 1000),
        },
        fill: {
            type: "gradient",
            gradient: { gradientToColors: ["#0be881"], stops: [0, 100] },
        },
        colors: ["#0fbcf9"],
        tooltip: {
            y: {
                formatter: (value) => `$ ${value.toFixed(3)}`,
            },
        },
    };

    const candleOptions: ApexOptions = {
        theme: {
            mode: isDark ? "dark" : "light",
        },
        chart: {
            type: "candlestick",
            height: 500,
            width: 500,
            // toolbar: { show: false },
            background: "transparent",
        },
        // title: {
        //   text: "CandleStick Chart",
        //   align: "left",
        // },
        xaxis: {
            labels: {
                // show: false,
                format: "M/dd",
            },
            axisBorder: {
                show: false,
            },
            axisTicks: {
                show: false,
            },
            type: "datetime",
        },
        yaxis: {
            // show: false,
            tooltip: {
                enabled: true,
            },
        },
    };

    const lineSeries: ApexAxisChartSeries = [
        {
            name: "price",
            data: data?.map((price) => price.close) as number[],
        },
    ];

    const candleSeries: ApexAxisChartSeries = [
        {
            name: "price",
            data: data?.map((price) => {
                return {
                    x: (typeof price.time_close === 'string' ? price.time_close.slice(0, 10) : price.time_close.toString().slice(0, 10)),
                    y: [
                        (typeof price.open === 'string' ? Number(price.open).toFixed(3) : price.open.toFixed(3)),
                        (typeof price.high === 'string' ? Number(price.high).toFixed(3) : price.high.toFixed(3)),
                        (typeof price.low === 'string' ? Number(price.low).toFixed(3) : price.low.toFixed(3)),
                        (typeof price.close === 'string' ? Number(price.close).toFixed(3) : price.close.toFixed(3)),
                    ],
                };
            }) as unknown as number[],
        },
    ];

    let currentSeries = candleSeries;
    let currentOptions = candleOptions;
    let currentType = "candlestick";

    const changeChart = () => {
        setCandle(!isCandle);
        // if (isCandle) {
        //   currentSeries = candleSeries;
        //   currentOptions = candleOptions;
        // } else {
        //   currentSeries = lineSeries;
        //   currentOptions = lineChartOptions;
        // }
    };

    return (
        <Container>
            {isLoading ? (
                "Loading Chart..."
            ) : (
                <ApexCharts
                    series={currentSeries}
                    options={currentOptions}
                    type="candlestick"
                />
            )}
            {/* <ToggleBtn onClick={changeChart}>Change</ToggleBtn> */}
        </Container>
    );
}

export default Chart;
